import "./App.css";
import { Component } from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import CardList from "./components/card-list/card-list.component.js";
import CardListSet3 from "./components/card-list/card-list-set3.components";
import NavBar from "./components/NavBar.js";
import Explore from "./components/explore/explore.component.js";
import BannerHook from "./components/banner/bannerHook.Component";
// import Banner from './components/banner/banner.component' 




class App extends Component {
  constructor() {
    super();

    this.state = {
      monsters: [],
      searchField: "",
      loading: false,
    };
  }

  componentDidMount() {
    fetch("https://jsonplaceholder.typicode.com/users")
      .then((response) => response.json())
      .then((users) => this.setState({ monsters: users }));
  }

  handleChange = (e) => {
    this.setState({ searchField: e.target.value });
  };
 

  render() {
    const { monsters, searchField } = this.state;
    const filteredMonsters = monsters.filter((monster) =>
      monster.name.toLowerCase().includes(searchField.toLowerCase())
    );

    return (
      <div>
        <Router>
       
           <BannerHook/>
            <NavBar handleChange={this.handleChange} />
            
            <Explore />
            <Switch>
            
              
            </Switch>
         
        </Router>
        <div className="monster-set2">
          <div className="set2">
            <h1> Shop </h1>
            <CardList monsters={filteredMonsters} />
          </div>
          <div className="btn">
            <button className="monster-card-btn">What's New</button>
            <button className="monster-card-btn">Drinks</button>
            </div>
        </div>
      {/* <Homepage monsters={filteredMonsters}/> */}
        <div className="monster-set3">
          <div className="set3">
            <h1>Discover</h1>
            <CardListSet3 monsters={filteredMonsters} />
            </div>
            <div className="btn">
            <button className="monster-card-btn">What's New</button>
            <button className="monster-card-btn">Drinks</button>
            </div>
          
        </div>
      </div>
    );
  }
}

